<template><undefined>
<h1>Dropdown 下拉菜单</h1>
<p>向下弹出的列表。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>当页面上的操作命令过多时，用此组件可以收纳操作元素。点击或移入触点，会出现一个下拉菜单。可在列表中进行选择，并执行相应的命令。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;                \n    &lt;v-dropdown :data=\&quot;data\&quot;&gt;\n        &lt;a href=\&quot;javascript:void(0)\&quot; class=\&quot;ant-dropdown-link ant-dropdown-trigger\&quot;&gt;\n            Hover me &lt;v-icon type=\&quot;down\&quot;&gt;&lt;/v-icon&gt;\n        &lt;/a&gt;\n    &lt;/v-dropdown&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    data:() =&gt; ({\n        data: [\n            {content: '1st item'},\n            {content: '2nd item'},\n            {content: '3rd item'},\n        ],\n    }),\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>                
    <v-dropdown :data="data">
        <a href="javascript:void(0)" class="ant-dropdown-link ant-dropdown-trigger">
            Hover me <v-icon type="down"></v-icon>
        </a>
    </v-dropdown>
</template>

</div>
                  <div slot="description"><h4 id="ji-ben"><a class="header-anchor" href="#ji-ben" aria-hidden="true">¶</a> 基本</h4>
<p>最简单的下拉菜单。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>                
    <span class="hljs-tag">&lt;<span class="hljs-name">v-dropdown</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"data"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"javascript:void(0)"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ant-dropdown-link ant-dropdown-trigger"</span>&gt;</span>
            Hover me <span class="hljs-tag">&lt;<span class="hljs-name">v-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"down"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-icon</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">v-dropdown</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">data</span>:<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> ({
        <span class="hljs-attr">data</span>: [
            {<span class="hljs-attr">content</span>: <span class="hljs-string">'1st item'</span>},
            {<span class="hljs-attr">content</span>: <span class="hljs-string">'2nd item'</span>},
            {<span class="hljs-attr">content</span>: <span class="hljs-string">'3rd item'</span>},
        ],
    }),
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
