</demo-box>
<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="dropdown-props"><a class="header-anchor" href="#dropdown-props" aria-hidden="true">¶</a> Dropdown Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>可选项数据源</td>
<td>Array</td>
<td>-</td>
</tr>
<tr>
<td>trigger</td>
<td>触发方式</td>
<td>String: <code v-pre="">hover</code> <code v-pre="">click</code></td>
<td>hover</td>
</tr>
<tr>
<td>position</td>
<td>下拉框的定位方式</td>
<td>String: <code v-pre="">absolute</code> <code v-pre="">fixed</code></td>
<td>absolute</td>
</tr>
<tr>
<td>popupContainer</td>
<td>下拉菜单渲染父节点。默认渲染到 body 上，如果你遇到菜单滚动定位问题，试试修改为滚动的区域，并相对其定位。</td>
<td>Function</td>
<td>() =&gt; document.body</td>
</tr>
</tbody>
</table>
<h3 id="dropdown-events"><a class="header-anchor" href="#dropdown-events" aria-hidden="true">¶</a> Dropdown Events</h3>
<table class="table"><thead>
<tr>
<th>事件</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>item-click</td>
<td>点击菜单项时触发</td>
<td>function(data)</td>
</tr>
</tbody>
</table>
<h3 id="dropdown-data-prop"><a class="header-anchor" href="#dropdown-data-prop" aria-hidden="true">¶</a> Dropdown:Data Prop</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>content</td>
<td>可选项数据源</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>divided</td>
<td>是否显示分割线</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>

export default {
    data:() => ({
        data: [
            {content: '1st item'},
            {content: '2nd item'},
            {content: '3rd item'},
        ],
        data1: [
            {content: '1st item'},
            {content: '2nd item'},
            {content: '3rd item', divided: true, disabled: true},
        ],
        data2: [
            {content: '1st item'},
            {content: '2nd item'},
            {
                content: 'sub',
                children: [
                    {content: '3rd item'},
                    {content: '4th item'},
                ],
            },
        ],
    }),
    methods: {
        itemClick(data) {
            console.log(data);
        }
    }
}
</script>


